export enum DataProvider {
  Mock = 'MOCK',
  FutWebApp  = 'FUT_WEB_APP',
}

export enum AppMode {
  Full  = 'FULL',
  Extension  = 'EXTENSION',
}

export interface AppSettings {
  appMode: AppMode;
  dataProvider: DataProvider;
}

if (process.env.REACT_APP_APP_MODE == undefined){
  throw new TypeError();
}

if (process.env.REACT_APP_DATA_PROVIDER == undefined){
  throw new TypeError();
}


export const appSettings: AppSettings = {
  appMode: <AppMode> process.env.REACT_APP_APP_MODE,
  dataProvider: <DataProvider> process.env.REACT_APP_DATA_PROVIDER,
 };
 


