import { Typography } from "@mui/material";
import styled from "styled-components";
import { ChallengeRequirement, ChallengeRequirementType } from "../services/interfaces";

const RequirementDescription = styled(Typography)(({ theme }) => ({
   fontSize: 12
}));


const SCOPE_NAME_DESCRIPTIONS: { [name: string]: string } = {
   'MORE': 'min.',
   'LESS': 'max.',
   'SAME': 'exactly',
}

interface ChallengeRequirementProps {
   requirement: ChallengeRequirement
}


export const ChallengeRequirementView: React.FC<ChallengeRequirementProps> = props => {
   const { requirement } = props

   switch (requirement.challenge_type_name) {
      case ChallengeRequirementType.OverallRequirement:
         return <RequirementDescription>
            {requirement.slot}. Squad rating: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.overall_value}
         </RequirementDescription>
      case ChallengeRequirementType.PlayersChemistryPointsRequirement:
         return <RequirementDescription>
            {requirement.slot}. Chemistry points on Each Player: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.chemistry_value}
         </RequirementDescription>
      case ChallengeRequirementType.ChemistryRequirement:
         return <RequirementDescription>
            {requirement.slot}. Squad chemistry points: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.chemistry_value}
         </RequirementDescription>
      case ChallengeRequirementType.PlayerQualityRequirement:
         return <RequirementDescription>
            {requirement.slot}. Player Quality: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.player_quality}
         </RequirementDescription>
      case ChallengeRequirementType.LeagueCountRequirement:
         return <RequirementDescription>
            {requirement.slot}. Leagues: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.count}
         </RequirementDescription>
      case ChallengeRequirementType.ClubCountRequirement:
         return <RequirementDescription>
            {requirement.slot}. Clubs: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.count}
         </RequirementDescription>
      case ChallengeRequirementType.NationCountRequirement:
         return <RequirementDescription>
            {requirement.slot}. Nationalities: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.count}
         </RequirementDescription>
      case ChallengeRequirementType.SameLeagueCountRequirement:
         return <RequirementDescription>
            {requirement.slot}. Same League Count: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.count}
         </RequirementDescription>
      case ChallengeRequirementType.SameClubCountRequirement:
         return <RequirementDescription>
            {requirement.slot}. Same Club Count: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.count}
         </RequirementDescription>
      case ChallengeRequirementType.SameNationCountRequirement:
         return <RequirementDescription>
            {requirement.slot}. Same Nation Count: {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.count}
         </RequirementDescription>
      case ChallengeRequirementType.NotImplementedRequirement:
         return <RequirementDescription>
            {requirement.slot}. Not Implemented
         </RequirementDescription>
      case ChallengeRequirementType.PlayerCountRequirement:
         return <RequirementDescription>
            {requirement.slot}. Players count {requirement.predicate_type} : {SCOPE_NAME_DESCRIPTIONS[requirement.scope]} {requirement.count}
         </RequirementDescription>
   }
}