import { Typography } from "@mui/material";
import styled from "styled-components";
import { ClubPlayerCard } from "../services/interfaces"

const _ASSETS_BASE_URL = "https://cdn.futbin.com/content/fifa24"
const PlayerContainer = styled.div`
   height: 120px;
`

const CardBackground = styled.div`
   border: none;
   background-position: center;
   background-size: contain;
   background-repeat: no-repeat;
   margin-right: auto;
   margin-left: auto;
   width: 80px;
   height: 100px;
   position: relative;
`

const PlayerAvatar = styled.img`
   left: 28px;
   top: 29px;
   width: 50px;
   position: absolute;
   width: 40px;
`

const PlayerClub = styled.img`
   left: 13px;
   top: 42px;
   width: 50px;
   position: absolute;
   width: 15px;
`

const PlayerNation = styled.img`
   left: 13px;
   top: 58px;
   width: 15px;
   position: absolute;
`

const PlayerOverall = styled.div`
   left: 13px;
   top: 20px;
   width: 15px;
   position: absolute;
`
const PlayerPosition = styled.div`
   left: 13px;
   top: 31px;
   width: 15px;
   position: absolute;
`

const PlayerName = styled(Typography)`
top: 94px;
width: 80px;
position: absolute;
`;

interface PlayerCardProps {
   playerCard: ClubPlayerCard
}

export const PlayerCard: React.FC<PlayerCardProps> = props => {
   const { playerCard } = props;
   var backgroundImageUrlId = playerCard.rare_flag + "_" + getCardResourceType(playerCard);

   return (
      <PlayerContainer>
         <CardBackground style={{
            backgroundImage: "url('" + _ASSETS_BASE_URL + "/img/cards/tiny/" + backgroundImageUrlId + ".png?v=141')"
         }}>
            <PlayerOverall>
               <Typography variant="h6" fontSize={11} align='center'>{playerCard.overall}</Typography>
            </PlayerOverall>
            <PlayerPosition>
               <Typography variant="h6" fontSize={8} align='center'>{playerCard.position}</Typography>
            </PlayerPosition>
            <PlayerClub alt={playerCard.team + ``} src={_ASSETS_BASE_URL + "/img/clubs/" + playerCard.team + ".png"} />
            <PlayerNation alt={playerCard.nationality + ``} src={_ASSETS_BASE_URL + "/img/nation/" + playerCard.nationality + ".png"} />
            <PlayerAvatar alt={playerCard.name} src={_ASSETS_BASE_URL + "/img/players/" + playerCard.asset_id + ".png?v=23"} />
            <PlayerName variant="h6" fontSize={8} align='center'>{playerCard.name}</PlayerName>
         </CardBackground>
      </PlayerContainer>
   )
}

function getCardResourceType(playerCard: ClubPlayerCard): string{
   if (playerCard.rare_flag == 72){
      return "heroes";
   }

   if (playerCard.rare_flag == 12){
      return "icon";
   }

   if (playerCard.rare_flag == 53){
      return "libertadores";
   }

   if (playerCard.rare_flag == 52){
      return "sudamericana";
   }

   if (playerCard.rare_flag == 11){
      return "tots";
   }

   if (playerCard.rare_flag == 5){
      return "toty";
   }

   return playerCard.card_quality.toString().toLowerCase()
}