import * as React from 'react';
import { connect } from "react-redux";
import { Dispatch } from "redux";
import Settings from '@mui/icons-material/Settings';
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import styled from 'styled-components';
import { Alert, AlertTitle, Button, Collapse, Paper, Snackbar, TextareaAutosize } from '@mui/material';
import { RootState, store } from '../store/store';
import { SquadBuildingChallenge, SolverResult, ClubPlayers } from '../services/interfaces';
import { getChallenge, getClubPlayers, getIgnoredClubPlayerCardsIds } from '../services/data_provider';
import { solverSlice, SolverState } from '../store/slices/solverSlice';
import Worker from "worker-loader!../services/worker/solver_worker.ts";
import { LoadingButton } from '@mui/lab';
import { SolverResultsView } from './SolverResultsView';
import SolverClubPlayerView from './SolverClubPlayersView';
import { SolverChallengeView } from './SolverChallengeView';
import { SportsSoccer } from '@mui/icons-material';


interface SolverViewProps {
  dispatch: Dispatch;
  clubPlayers: ClubPlayers | null,
  ignoredClubPlayerCardsIds: number[],
  challenge: SquadBuildingChallenge | null,
  solverResult: SolverResult | null,
  solverConfig: { [key: string]: any },
  solverState: SolverState,
}

interface SolverViewState {
  isConfigCollapsed: boolean
  solverConfigString: string
  successAlertOpen: boolean
  errorAlertOpen: boolean
}

const RootBox = styled(Box)(({ theme }) => ({
  ...theme.typography.body2,
  height: 'calc(100vh - 40px)',
  overflow: 'auto',
}));


class SolverView extends React.Component<SolverViewProps, SolverViewState>{
  constructor(props: SolverViewProps) {
    super(props)
    this.state = {
      successAlertOpen: false,
      errorAlertOpen: false,
      isConfigCollapsed: true,
      solverConfigString: JSON.stringify(props.solverConfig, null, 2)
    };

    this.solveChallenge = this.solveChallenge.bind(this)
    this.showConfig = this.showConfig.bind(this)
  }

  componentDidMount() {
    this.loadClubPlayers()
    this.loadChallenge();
    this.loadIgnoredClubPlayerCardsIds();
    if (chrome.runtime != undefined){
      chrome.runtime.onMessage.addListener(function (request, sender, sendResponse) {
        console.log(request);
        const { solverResult } = request
        store.dispatch(solverSlice.actions.setSolverResult(solverResult));
        return true
      });
    }


    window.addEventListener("focus", () => {
      this.loadClubPlayers()
      this.loadChallenge();
      this.loadIgnoredClubPlayerCardsIds();
    });
  }

  loadIgnoredClubPlayerCardsIds() {
    getIgnoredClubPlayerCardsIds().then(ignored_player_ids => {
      this.props.dispatch(solverSlice.actions.setIgnoredClubPlayerCardsIds(ignored_player_ids));
    });
  }

  loadClubPlayers() {
    console.log('loadClubPlayers');
    getClubPlayers().then(clubPlayers => {
      this.props.dispatch(solverSlice.actions.setClubPlayers(clubPlayers));
    });
  }

  loadChallenge() {
    getChallenge().then(challenge => {
      this.props.dispatch(solverSlice.actions.setChallenge(challenge));
    });
  }

  showConfig() {
    this.setState({
      ...this.state,
      isConfigCollapsed: !this.state.isConfigCollapsed
    })
  }

  onConfigChange(e: React.ChangeEvent<HTMLTextAreaElement>) {
    this.setState({
      ...this.state,
      solverConfigString: e.target.value
    })
  }

  setAlertOpen(name: string, open: boolean) {
    const nameAlertOpen = name + 'AlertOpen'
    this.setState({
      ...this.state,
      [nameAlertOpen]: open
    })
  }

  solveChallenge() {
    const { challenge, clubPlayers, ignoredClubPlayerCardsIds } = this.props;
    const { solverConfigString } = this.state;
    const solverConfig = JSON.parse(solverConfigString);
    this.props.dispatch(solverSlice.actions.setSolverStateRunning());

    if (chrome.runtime != undefined){
      chrome.runtime.sendMessage(chrome.runtime.id, {
        challenge: challenge,
        clubPlayerCards: clubPlayers?.club_players_cards.filter(
          player_card => !ignoredClubPlayerCardsIds.includes(player_card.asset_id
          )),
        solverConfig: solverConfig
      }, function (response) {
        {
          console.log(response);
          const { solverResult } = response
          store.dispatch(solverSlice.actions.setSolverResult(solverResult));
        }
      });
    }else{
      const worker = new Worker();

      worker.addEventListener("message", (e) => {
        const { solverResult } = e.data
        this.setAlertOpen('success', true)
        this.props.dispatch(solverSlice.actions.setSolverResult(solverResult));
      });

      worker.postMessage({
        challenge: challenge,
        clubPlayerCards: clubPlayers?.club_players_cards.filter(
          player_card => !ignoredClubPlayerCardsIds.includes(player_card.asset_id
          )),
        solverConfig: solverConfig
      });
    }

  }

  render(): React.ReactNode {
    const { challenge, clubPlayers, solverState, solverResult } = this.props;
    const { isConfigCollapsed, solverConfigString, errorAlertOpen, successAlertOpen } = this.state;

    return (
      <RootBox
        sx={{
          bgcolor: 'background.paper',
          pt: 12,
          pb: 6,
        }}
      >
        <Container>
          <Snackbar
            open={successAlertOpen}
            anchorOrigin={{ vertical: 'bottom', horizontal: 'left' }}
            autoHideDuration={3000} onClose={() => this.setAlertOpen('success', false)}
          >
            <Alert severity="success" sx={{ width: '100%' }} onClose={() => this.setAlertOpen('success', false)}>
              <AlertTitle>Success!</AlertTitle>
              Your solver results are ready
            </Alert>
          </Snackbar>
          <Snackbar
            open={errorAlertOpen}
            anchorOrigin={{ vertical: 'bottom', horizontal: 'left' }}
            autoHideDuration={3000} onClose={() => this.setAlertOpen('error', false)}
          >
            <Alert severity="error" sx={{ width: '100%' }} onClose={() => this.setAlertOpen('error', false)}>
              <AlertTitle>Error!</AlertTitle>
              An error occured
            </Alert>
          </Snackbar>
          <Grid container spacing={2}>
            <Grid item xs={12}>{
              <SolverClubPlayerView />
            }
            </Grid>
            <Grid item xs={12}>{
              <SolverChallengeView challenge={challenge} />
            }
            </Grid>
          </Grid>
          <Box sx={{ pt: 2 }}>
            <Button variant="contained" onClick={this.showConfig} disableElevation fullWidth startIcon={<Settings />}>
              Config
            </Button>
          </Box>
          <Collapse in={!isConfigCollapsed}>
            <Box sx={{ pt: 2 }}>
              <TextareaAutosize
                style={{ width: '100%', maxWidth: '100%' }}
                value={solverConfigString}
                onChange={e => this.onConfigChange(e)}
              />
            </Box>
          </Collapse>
          <Box sx={{ pt: 2 }}>
            <LoadingButton
              loadingPosition='start'
              variant="contained"
              onClick={this.solveChallenge}
              startIcon={<SportsSoccer />}
              disableElevation
              fullWidth
              loading={solverState == SolverState.RUNNING}
            >
              Solve
            </LoadingButton>
          </Box>
          <Box sx={{ pt: 2 }}>
            {solverResult ? <SolverResultsView solverResults={solverResult} /> : null}
          </Box>
        </Container >
      </RootBox>
    )
  }
}

const mapStateToProps = (state: RootState) => {
  return ({
    challenge: state.solverSlice.challenge,
    ignoredClubPlayerCardsIds: state.solverSlice.ignoredClubPlayerCardsIds,
    clubPlayers: state.solverSlice.clubPlayers,
    solverConfig: state.solverSlice.solverConfig,
    solverState: state.solverSlice.solverState,
    solverResult: state.solverSlice.solverResult,
  });
}

export default connect(mapStateToProps)(SolverView);
