import * as React from 'react';
import { Avatar, Badge, Box, Button, Chip, Collapse, Grid, Paper, Stack, Table, TableBody, TableCell, tableCellClasses, TableContainer, TableHead, TableRow, TableSortLabel, Typography } from '@mui/material';
import { ChallengeRequirement, SolverResult, SolverSolution, SolverSolutionRequirement } from '../services/interfaces';
import { PlayerCard } from './PlayerCard';
import styled from 'styled-components';
import { blue, grey } from '@mui/material/colors';
import { visuallyHidden } from '@mui/utils';
import { getFormationPositions } from '../services/data_provider';
import CheckCircle from '@mui/icons-material/CheckCircle';
import { ChallengeRequirementView } from './RequirementView';

function mode(arr: number[]): number | undefined {
   const counts: { [name: number]: number } = {};
   let maxCount = 0;
   let maxKey;

   for (let i = 0; i < arr.length; i++) {
      const key = arr[i];
      const count = (counts[key] = (counts[key] || 0) + 1);
      if (count > maxCount) {
         maxCount = count;
         maxKey = key;
      }
   }

   return maxKey;
}

const StyledTableCell = styled(TableCell)(({ theme }) => ({
   [`&.${tableCellClasses.head}`]: {
      backgroundColor: theme.palette.primary.main,
      color: theme.palette.common.white,
   },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
   '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
   },
   '&:last-child td, &:last-child th': {
      border: 0,
   },
}));

const DescriptionIcon = styled.img`
   width: 32px;
`

interface SolverResultsProps {
   solverResults: SolverResult
}

interface SolverResultsState {
   collapsedIndex: number | null
   order: Order
   orderBy: keyof SolverSolution
}

interface SquadPropertyHeadCell {
   id: keyof SolverSolution;
   label: string;
}

type Order = 'asc' | 'desc';

const squadPropertiesHeadCells: readonly SquadPropertyHeadCell[] = [
   {
      id: 'overall',
      label: 'Overall',
   },
   {
      id: 'chemistry',
      label: 'Chemistry',
   },
   {
      id: 'invalid_requirements_count',
      label: 'Invalid requirements',
   },
];

export class SolverResultsView extends React.Component<SolverResultsProps, SolverResultsState> {
   constructor(props: SolverResultsProps) {
      super(props)
      this.state = {
         collapsedIndex: null,
         order: 'asc',
         orderBy: 'chemistry'
      };

      this.collapseRow = this.collapseRow.bind(this);
      this.handleSort = this.handleSort.bind(this);
   }

   collapseRow(index: number | null) {
      const { collapsedIndex } = this.state;

      if (collapsedIndex != index) {
         this.setState({
            ...this.state,
            collapsedIndex: index
         })
      } else {
         this.setState({
            ...this.state,
            collapsedIndex: -1
         })
      }
   }

   saveSquadToBuild(solution: SolverSolution) {
      if (chrome?.runtime !== undefined) {
         chrome.runtime.sendMessage(chrome.runtime.id, {
            message_type: 'SOLVE_WITH_SOLUTION',
            data: JSON.stringify(solution)
         });
      } else {
         console.log('Chrome runtime unavailable')
      }

   }

   handleSort(property: keyof SolverSolution) {
      const { orderBy, order } = this.state;
      const isAsc = orderBy === property && order === 'asc';
      this.setState({
         ...this.state,
         order: isAsc ? 'desc' : 'asc',
         orderBy: property
      });
   }

   render(): React.ReactNode {
      const { collapsedIndex, orderBy, order } = this.state;
      const { solverResults } = this.props;
      const formationPositions = getFormationPositions(solverResults.challenge.formation_name);
      var solutions: SolverSolution[] = Object.assign([], solverResults.solutions);
      var requirements: ChallengeRequirement[] = Object.assign([], solverResults.challenge.challenge_requirements);
      requirements = requirements.sort((req1, req2) => {
         return req1.slot > req2.slot ? 1 : -1;
      });

      if (order === 'asc') {
         solutions = solutions.sort((o1: SolverSolution, o2: SolverSolution) => (o1[orderBy] < o2[orderBy] ? -1 : 1));
      } else {
         solutions = solutions.sort((o1: SolverSolution, o2: SolverSolution) => (o1[orderBy] > o2[orderBy] ? -1 : 1));
      }

      return (
         <TableContainer component={Paper}>
            <Table aria-label="collapsible table">
               <TableHead>
                  <TableRow>
                     <StyledTableCell align="center" width="5%">MVP</StyledTableCell>
                     <StyledTableCell align="center" width="4%" colSpan={3}>Squad</StyledTableCell>
                     {
                        squadPropertiesHeadCells.map(headCell => <StyledTableCell align="center" colSpan={1}>
                           <TableSortLabel
                              active={orderBy === headCell.id}
                              direction={orderBy === headCell.id ? order : 'asc'}
                              onClick={() => this.handleSort(headCell.id)} >
                              {headCell.label}
                              {orderBy === headCell.id ? (
                                 <Box component="span" sx={visuallyHidden}>
                                    {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                                 </Box>
                              ) : null}
                           </TableSortLabel>
                        </StyledTableCell>)
                     }
                  </TableRow>
               </TableHead>
               <TableBody>
                  {solutions.map((solution, index) => {

                     var requirements: SolverSolutionRequirement[] = Object.assign([], solution.requirements);
                     requirements = requirements.sort((req1, req2) => {
                        return req1.slot > req2.slot ? 1 : -1;
                     });
                     const best_player_card = solution.player_cards.reduce(function (prev, curr) {
                        return prev.overall > curr.overall ? prev : curr;
                     });
                     const nationality_mode = mode(solution.player_cards.map(player_card => player_card.nationality))
                     const league_mode = mode(solution.player_cards.map(player_card => player_card.league))
                     const team_mode = mode(solution.player_cards.map(player_card => player_card.team))

                     return (
                        <React.Fragment>
                           <StyledTableRow key={index} onClick={() => this.collapseRow(index)}>
                              <TableCell align="center"><PlayerCard playerCard={best_player_card} /></TableCell>
                              <TableCell align="center"><DescriptionIcon alt={nationality_mode + ``} src={"https://cdn.futbin.com/content/fifa23/img/nation/" + nationality_mode + ".png"} /></TableCell>
                              <TableCell align="center">
                                 <Avatar sx={{ bgcolor: blue[200], width: 64, height: 64 }} >
                                    <DescriptionIcon alt={league_mode + ``} src={"https://cdn.futbin.com/content/fifa23/img/league/" + league_mode + ".png"} />
                                 </Avatar>
                              </TableCell>
                              <TableCell align="center"><DescriptionIcon alt={team_mode + ``} src={"https://cdn.futbin.com/content/fifa23/img/clubs/" + team_mode + ".png"} /></TableCell>
                              <TableCell align="center">{solution.overall}</TableCell>
                              <TableCell align="center">{solution.chemistry}</TableCell>
                              <TableCell align="center">
                                 <Stack direction="row" spacing={0.5} justifyContent="center">
                                    {

                                       requirements.map(
                                          requirement => {
                                             var color: 'success' | 'warning' | 'error';
                                             if (requirement.is_implemented) {
                                                if (requirement.is_valid) {
                                                   color = 'success';
                                                } else {
                                                   color = 'error';

                                                }
                                             } else {
                                                color = 'warning';
                                             }
                                             return <Chip size="small" color={color} label={requirement.slot} />
                                          }

                                       )

                                    }
                                 </Stack>
                              </TableCell>
                           </StyledTableRow>
                           <TableRow >
                              <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={8}>
                                 <Collapse in={collapsedIndex == index} timeout="auto" unmountOnExit>
                                    <Box sx={{ pt: 2 }}>
                                       <Grid style={{ textAlign: 'center' }} key={index} container columns={11} >
                                          {solution.player_cards.map(
                                             (playerCard, playerIndex) => {
                                                return (
                                                   <Grid key={playerCard.id} xs={1} item>
                                                      <Chip size="small" label={formationPositions[playerIndex]} />
                                                      <PlayerCard playerCard={playerCard} />
                                                   </Grid>
                                                )
                                             }
                                          )}
                                       </Grid>
                                       <Button onClick={() => this.saveSquadToBuild(solution)}> Build this squad </Button>
                                    </Box>
                                 </Collapse>
                              </TableCell>
                           </TableRow>
                        </React.Fragment>
                     )
                  })}
               </TableBody>
            </Table>
         </TableContainer >

      );
   }
}