import React from "react";
import { Header } from "./Header";
import { Footer } from "./Footer";
import { RootState } from "../store/store";
import { connect } from "react-redux";
import styled from 'styled-components';
import SolverView from "./SolverView";
import { Box, Button } from "@mui/material";
import { SportsSoccer } from "@mui/icons-material";

interface ExtensionPanelViewState {
   isPanelCollapsed: boolean
}
interface ExtensionPanelViewProps { }

const ExtensionAppContainer = styled.div`
  z-index: 100;
  width: 30%;
  height: 100%;
  position: absolute;
  left: 70%;
  overflow-y: none;
`

const ExtensionButtonContainer = styled.div`
  z-index: 100;
  position: absolute;
  left: 70%;
  overflow-y: none;
`

const ExtensionButton = styled(Button)(({ theme }) => ({
   ...theme.typography.body2,
   padding: '20px 32px 20px 32px',
}));


class ExtensionPanelView extends React.Component<ExtensionPanelViewProps, ExtensionPanelViewState>{
   constructor(props: ExtensionPanelViewProps) {
      super(props)
      this.state = {
         isPanelCollapsed: true,
      };

      this.setPanelCollapsed = this.setPanelCollapsed.bind(this)
   }

   setPanelCollapsed(e: React.MouseEvent<HTMLElement>) {
      this.setState({
         ...this.state,
         isPanelCollapsed: !this.state.isPanelCollapsed
      })
   }

   render(): React.ReactNode {
      const { isPanelCollapsed } = this.state;
      const fullApp = <ExtensionAppContainer>
         <Header onHideButtonClicked={this.setPanelCollapsed} />
         <Box sx={{ display: { xl: isPanelCollapsed ? 'block' : 'none', xs: 'none' } }}>
            <SolverView />
         </Box>
         <Footer />
      </ExtensionAppContainer>;
      const extensionButton = <ExtensionButtonContainer>
         <ExtensionButton variant="contained" onClick={this.setPanelCollapsed}>
            <SportsSoccer sx={{ color: 'white' }} />
         </ExtensionButton>
      </ExtensionButtonContainer>;
      return isPanelCollapsed ? fullApp : extensionButton;
   }
}



const mapStateToProps = (state: RootState) => {
   return ({});
}

export default connect(mapStateToProps)(ExtensionPanelView);