import styled from 'styled-components';
import { Box, Paper, Typography } from '@mui/material';
import { ChallengeRequirement, ChallengeRequirementType, SquadBuildingChallenge } from '../services/interfaces';
import { ChallengeRequirementView } from './RequirementView';

const ChallengeContainer = styled(Paper)(({ theme }) => ({
   backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
   ...theme.typography.body2,
   padding: theme.spacing(1),
   textAlign: 'center',
   minHeight: '210px',
}));




interface SolverChallengeViewProps {
   challenge: SquadBuildingChallenge | null
}

function NoChallenge() {
   return <Typography>No challenge imported</Typography>
}

function ChallengeView(props: { challenge: SquadBuildingChallenge; }) {
   const { challenge } = props;
   const requirements: ChallengeRequirement[] = Object.assign([], challenge.challenge_requirements);

   return <Box sx={{ pl: 2, pr: 2 }}>
      <Typography variant="h6" fontSize={14}>{challenge.name}</Typography>
      {requirements.sort((req1, req2) => {
         return req1.slot > req2.slot ? 1 : -1;
      }).map(
         requirement => <ChallengeRequirementView key={requirement.slot} requirement={requirement} />
      )}
   </Box>
}

export const SolverChallengeView: React.FC<SolverChallengeViewProps> = props => {
   const { challenge } = props;

   return <ChallengeContainer>
      <Typography variant="h6" fontSize={22} color='primary'>Challenge to solve</Typography>
      <Box sx={{ pt: 2 }}>
         {!challenge ?
            <NoChallenge />
            :
            <ChallengeView challenge={challenge} />
         }
      </Box>
   </ChallengeContainer>
}