import { createSlice } from '@reduxjs/toolkit'
import type { PayloadAction } from '@reduxjs/toolkit'

import { SquadBuildingChallenge, SolverResult, ClubPlayers, ClubPlayerCard } from '../../services/interfaces'
import default_solver_config from '../../services/worker/default_solver_config.json';

export enum SolverState {
  NOT_STARTED = 0,
  RUNNING = 1,
  FINISHED = 2,
}

export type SolverSliceState = {
  clubPlayers: ClubPlayers | null,
  ignoredClubPlayerCardsIds: number[],
  challenge: SquadBuildingChallenge | null,
  solverConfig: { [key: string]: any },
  solverState: SolverState,
  solverResult: SolverResult | null;
}

const initialState: SolverSliceState = {
  clubPlayers: null,
  ignoredClubPlayerCardsIds: [],
  challenge: null,
  solverConfig: default_solver_config,
  solverState: SolverState.NOT_STARTED,
  solverResult: null
}

export const solverSlice = createSlice({
  name: 'SolverSlice',
  initialState,
  reducers: {
    setClubPlayers: (state, action: PayloadAction<ClubPlayers | null>) => {
      if (action.payload != null){
        var playerCards: ClubPlayerCard[] = Object.assign([], action.payload.club_players_cards);
        playerCards = playerCards.sort((req1, req2) => {
           return req1.overall > req2.overall ? -1 : 1;
        });
        action.payload.club_players_cards = playerCards
      }

      return {
        ...state,
        clubPlayers: action.payload
      }
    },
    setIgnoredClubPlayerCardsIds: (state, action: PayloadAction<number[]>) => {
      return {
        ...state,
        ignoredClubPlayerCardsIds: action.payload
      }
    },
    setChallenge: (state, action: PayloadAction<SquadBuildingChallenge | null>) => {
      return {
        ...state,
        challenge: action.payload
      }
    },
    setSolverStateRunning: (state) => {
      return {
        ...state,
        solverState: SolverState.RUNNING,
        solverResult: null,
      }
    },
    setSolverResult: (state, action: PayloadAction<SolverResult>) => {
      return {
        ...state,
        solverState: SolverState.FINISHED,
        solverResult: action.payload
      }
    },
    handleIgnoreClubPlayerCard: (state, action: PayloadAction<ClubPlayerCard>) => {
      var { ignoredClubPlayerCardsIds } = state;
      const clubPlayerCard: ClubPlayerCard = action.payload;
      sessionStorage.setItem('IGNORED_CLUB_PLAYER_CARD_IDS', JSON.stringify(ignoredClubPlayerCardsIds));

      if (ignoredClubPlayerCardsIds.includes(clubPlayerCard.asset_id)) {
        const index = ignoredClubPlayerCardsIds.indexOf(clubPlayerCard.asset_id);
        ignoredClubPlayerCardsIds.splice(index, 1);
      } else {
        ignoredClubPlayerCardsIds.push(clubPlayerCard.asset_id)
      }
    },
  },
})
