import styled from 'styled-components';
import { Box, Button, Checkbox, Dialog, Divider, Grid, IconButton, ImageList, ListItemButton, ListItemIcon, Paper, Stack, Typography } from '@mui/material';
import ListItemText from '@mui/material/ListItemText';
import ListItem from '@mui/material/ListItem';
import List from '@mui/material/List';
import { ClubPlayerCard, ClubPlayers } from "../services/interfaces";
import { PlayerCard } from './PlayerCard';
import React from 'react';
import { RootState } from '../store/store';
import { connect } from 'react-redux';
import { solverSlice } from '../store/slices/solverSlice';
import { Dispatch } from '@reduxjs/toolkit';
import Edit from '@mui/icons-material/Edit';
import { DisabledByDefaultRounded } from '@mui/icons-material';

const PlayersContainer = styled(Paper)(({ theme }) => ({
   backgroundColor: theme.palette.mode === 'dark' ? '#1A2027' : '#fff',
   ...theme.typography.body2,
   padding: theme.spacing(1),
   textAlign: 'center',
   minHeight: '210px',
   minWidth: '150px',
}));

const InfoContainer = styled.div`
   margin-top: 24px;
   align-items: center;
`

const DisabledIconContainer = styled.div`
   position: absolute;
   top: 10px;
   z-index: 100;
`

const StyledListItemButton = styled(ListItemButton)(({ theme }) => ({
   "&.Mui-selected, &.Mui-selected:hover": {
      backgroundColor: '#00000088'
   }
}));

interface SolverClubPlayerViewProps {
   dispatch: Dispatch;
   clubPlayers: ClubPlayers | null
   ignoredClubPlayerCardsIds: number[]
}

interface SolverClubPlayerViewState {
   isPlayersDialogOpen: boolean,
}


function NoClubPlayers() {
   return <Typography>No players imported</Typography>
}

function ClubPlayersView(props: { clubPlayers: ClubPlayers; ignoredClubPlayerCardsIds: number[]; isPlayersDialogOpen?: any; handleDialogOpen?: any; handlePlayerSelect?: any; }) {
   const { clubPlayers, ignoredClubPlayerCardsIds, isPlayersDialogOpen, handlePlayerSelect, handleDialogOpen } = props;
   var playerCards: ClubPlayerCard[] = Object.assign([], clubPlayers.club_players_cards);
   const groupedPlayers = playerCards.reduce((groups: { [key: number]: [ClubPlayerCard] }, playerCard) => {
      const groupBy = Math.floor(playerCard.overall / 10);
      const group = (groups[groupBy] || []);
      group.push(playerCard);
      groups[groupBy] = group;
      return groups;
   }, {});

   var date = new Date(props.clubPlayers.timestamp);

   return <Box sx={{ pl: 2, pr: 2 }}>
      <Grid container justifyContent="center">
         <Grid item xs={4}>
            <Typography variant="h6" fontSize={14}>Best player:</Typography>
            <PlayerCard playerCard={playerCards[0]} />
         </Grid>
         <Grid item xs={4}>
            <Typography variant="h6" fontSize={14}>Num players:</Typography>
            <InfoContainer>
               <Stack direction={'row'} justifyContent="center">
                  <Typography variant="h1" fontSize={36} color='primary'>{props.clubPlayers.club_players_cards.length}</Typography>
                  <IconButton color="primary" aria-label="upload picture" component="label" onClick={() => handleDialogOpen(true)}>
                     <Edit />
                  </IconButton>
               </Stack>
            </InfoContainer>
         </Grid>
         <Grid item xs={4} >
            <Typography variant="h6" fontSize={14}>Date imported:</Typography>
            <InfoContainer>
               <Typography variant="h1" fontSize={14}>{date.toLocaleDateString()}</Typography>
               <Typography variant="h1" fontSize={14}>{date.toLocaleTimeString()}</Typography>
            </InfoContainer>
         </Grid>
      </Grid>
      <Dialog
         open={isPlayersDialogOpen}
         maxWidth='xl'
         onClose={() => handleDialogOpen(false)}
      >

         {
            Object.entries(groupedPlayers).reverse().map(([overall, overallPlayerCards]) => <div>
               <ImageList cols={6}>
                  {overallPlayerCards.map(
                     player_card => <StyledListItemButton key={player_card.id}
                        onClick={() => handlePlayerSelect(player_card)}
                     >
                        <DisabledIconContainer>
                           <DisabledByDefaultRounded sx={{ display: ignoredClubPlayerCardsIds.includes(player_card.asset_id) ? 'block' : 'none' }} />
                        </DisabledIconContainer>
                        <PlayerCard playerCard={player_card} />
                     </StyledListItemButton>
                  )
                  }

               </ImageList>
               <Divider />
            </div>)
         }

      </Dialog>
   </Box>
}

class SolverClubPlayerView extends React.Component<SolverClubPlayerViewProps, SolverClubPlayerViewState> {
   constructor(props: SolverClubPlayerViewProps) {
      super(props)
      this.state = {
         isPlayersDialogOpen: false,
      };

      this.handleDialogOpen = this.handleDialogOpen.bind(this);
      this.handlePlayerSelect = this.handlePlayerSelect.bind(this);
   }

   handleDialogOpen(isOpen: boolean) {
      this.setState({
         ...this.state,
         isPlayersDialogOpen: isOpen
      })
   }

   handlePlayerSelect(playerCard: ClubPlayerCard) {
      this.props.dispatch(solverSlice.actions.handleIgnoreClubPlayerCard(playerCard));
   }

   render(): React.ReactNode {
      const { isPlayersDialogOpen } = this.state;
      const { clubPlayers, ignoredClubPlayerCardsIds } = this.props;

      return <PlayersContainer>
         <Typography variant="h6" fontSize={22} color='primary'>Club players</Typography>
         <Box sx={{ pt: 2 }}>
            {!clubPlayers ?
               <NoClubPlayers />
               :
               <ClubPlayersView
                  clubPlayers={clubPlayers}
                  isPlayersDialogOpen={isPlayersDialogOpen}
                  handleDialogOpen={this.handleDialogOpen}
                  handlePlayerSelect={this.handlePlayerSelect}
                  ignoredClubPlayerCardsIds={ignoredClubPlayerCardsIds}
               />
            }
         </Box>


      </PlayersContainer>
   }
}

const mapStateToProps = (state: RootState) => {
   return ({
      clubPlayers: state.solverSlice.clubPlayers,
      ignoredClubPlayerCardsIds: state.solverSlice.ignoredClubPlayerCardsIds,
   });
}

export default connect(mapStateToProps)(SolverClubPlayerView);